import {
  Card,
  CardHeader,
  CardTitle,
  CardBody,
  FormGroup,
  Row,
  Col,
  Input,
  Form,
  Button,
  Label,
  CustomInput
  
} from 'reactstrap' 
import React, { useState, useEffect } from 'react'
// import { viewcricketcontestprize, editcricketcontestprize } from '../../store/actions'
import { useSelector } from 'react-redux'
import '@styles/react/libs/flatpickr/flatpickr.scss'

const VerticalForm = () => {
  
  // const dispatch = useDispatch()
  const store = useSelector(state => state.prizemaster)
  const {view_cricket_contest_prize} = store
 
  const [editcontestprizeform, setEditcontestprizeform] = useState()
   
  // useEffect(() => {
  //   dispatch(viewcricketcontestprize)
  
  // }, [view_cricket_contest_prize])

  useEffect(() => {
    setEditcontestprizeform({contest_id:view_cricket_contest_prize?.contest_id, name:view_cricket_contest_prize?.name, caption:view_cricket_contest_prize?.caption, type:view_cricket_contest_prize?.type, value:view_cricket_contest_prize?.value, prize_rank:view_cricket_contest_prize?.prize_rank, assets_url:view_cricket_contest_prize?.assets_url })
  }, [view_cricket_contest_prize])
  const onSubmit = (e) => {
    e.preventDefault()
    // dispatch(editcricketcontestprize(view_cricket_contest_prize.id, editcontestprizeform))
  }

  const convertBase64 = (file) => {
    return new Promise((resolve, reject) => {
      const fileReader = new FileReader()
      fileReader.readAsDataURL(file)
      
      fileReader.onload = () => {
        resolve(fileReader.result)
      }
      
      fileReader.onerror = (error) => {
        reject(error)
      }
    })
  }

  const uploadImage = async (e) => {
    const file = e.target.files[0]
    const base64 = await convertBase64(file)
    setEditcontestprizeform({ ...editcontestprizeform, assets_url: base64 })
  }
  const type = 'coupons'
  return (
    <Card>
      <CardHeader>
        <CardTitle tag="h4">Edit Football Contest Prizes</CardTitle>
      </CardHeader>
      <CardBody>
        <Form>
          <Row>
            {type === 'prizes' ? 
          <Col sm="12">
              <FormGroup>
                <Label for="EmailVertical">Value</Label>
                <Input
                  type="number"
                  name="value"
                  id="value"
                  value={editcontestprizeform?.contest_id}
                  onChange={(e) => setEditcontestprizeform({ ...editcontestprizeform, contest_id: e.target.value })}
                  placeholder="Enter value"
                />
              </FormGroup>
            </Col>
: ''}
            <Col sm="12">
              <FormGroup>
                <Label>Name</Label>
                <Input
                  type="text"
                  name="name"
                  id="nameVertical"
                  value={editcontestprizeform?.name}
                  onChange={(e) => setEditcontestprizeform({ ...editcontestprizeform, name: e.target.value })}
                  placeholder="Enter Product Name"
                />
              </FormGroup>
            </Col>
            <Col>
              <FormGroup>
                <Label for="nameVertical">Caption</Label>
                <Input
                  type="text"
                  name="name"
                  id="nameVertical"
                  value={editcontestprizeform?.caption}
                  onChange={(e) => setEditcontestprizeform({ ...editcontestprizeform, caption: e.target.value })}
                  placeholder="Enter Product Name"
                />
              </FormGroup>
            </Col>
            { type === 'coupons' && ' senemail !== null' ?
            <Col sm="12">
              <FormGroup>
                <Label for="EmailVertical">Send mail</Label>
                <Input
                  type="number"
                  name="Email"
                  id="EmailVertical"
                  value={editcontestprizeform?.value}
                  onChange={(e) => setEditcontestprizeform({ ...editcontestprizeform, value: e.target.value })}
                  placeholder="Enter No of Coins "
                />
              </FormGroup>
            </Col>     : ''
}     
{type === ('coupons' || 'prizes') ?
            <Col sm="12">
              <FormGroup>
                <Label for="exampleCustomFileBrowser">Custom File Input</Label>
                <CustomInput
                  type="file"
                  id="exampleCustomFileBrowser"
                  name="customFile"
                  onChange={(e) => uploadImage(e)}
                />
              </FormGroup>
            </Col> : ''
}
            <Col sm="12">
              <FormGroup className="d-flex mb-0">
                <Button.Ripple
                  className="mr-1"
                  color="primary"
                  type="submit"
                  onClick={(e) => onSubmit(e)}
                >
                  Submit
                </Button.Ripple>
                {/* <Button.Ripple
                  outline
                  color="secondary"
                  type="reset"
                  onClick={(e) => onclear(e)}
                >
                  Reset
                </Button.Ripple> */}
              </FormGroup>
            </Col>
          </Row>
        </Form>
      </CardBody>
    </Card>
  )
}
export default VerticalForm

